import React from 'react';

export default class HomeText extends React.Component {

    render() {
        return (
        	<div>
        	<div id="#cover"></div>
            <section className="content content--layout text-render">
				<h2 className="word word--playful text-render-word">ROHIT GIRDHAR</h2>
			</section>
            <div className="content">
              <div className="content__container">
                <p className="content__container__text">
                  Checkout
                </p>

                <ul className="content__container__list">
                <li className="content__container__list__item" id="visitabout">About</li>
                  <li className="content__container__list__item" id="visitcode">Code</li>
                  <li className="content__container__list__item" id="visitdesign">Design</li>
                </ul>
              </div>
            </div>
           </div>
        );
    }
}



